body {
  margin: 0;
  font-family: 'Bungee', cursive;
  background-color: #2c3e50;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.container {
  text-align: center;
  background-color: #ecf0f1;
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideIn 0.5s ease-in-out;
}

h1, h2 {
  color: #34495e;
}

button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 16px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin-top: 20px;
}

button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
}

select {
  width: 100%;
  padding: 15px;
  margin: 10px 0;
  border: 2px solid #34495e;
  border-radius: 10px;
  background-color: #ecf0f1;
  font-size: 16px;
}

@keyframes slideIn {
  from {
      transform: translateY(-100%);
      opacity: 0;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}
